import { Component, OnInit, Output , EventEmitter  } from '@angular/core';


@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  name : String ="TARUN PATHAK";
   isCollapsed = false;
  @Output('menuItmSelected') menuSelected =  new EventEmitter<string>();
  // ,'Courses'
  menuItems : string[] = ['Expriences','Projects','Technologies','Education','About Me'];
  
  constructor() { }

  ngOnInit() {
  }

  OnMenuSelect(SelectedMenuItem :string)
  {
    console.log("selected :"+SelectedMenuItem)
    this.menuSelected.emit(SelectedMenuItem);
  }

}
